.body{
       
    background-image: linear-gradient(to right, #8360c3, #2ebf91);
    margin-top: 0px;

      
    
}
section{
    align-items: center;
    text-align: center;
    /* margin: 30px 0px 30px; */
 
   
}

.centercontainer{
   display: flex;
   height: 100%;
   justify-content: space-between;
}
.centercontainer img{
   
    margin-top: 1px;
}
.heading h2{
      color: rgb(39, 25, 8);
      font-size: 30px;
}
.heading_section{
    
    text-align: center;
}
.heading .livetext{
    font-size: 30px;
    font-weight: 700;
}
#live{
    width: 5px;
    height: 2px;
    justify-content: space-between;
    background: rgb(255, 4, 4);
    border-radius: 50%;
    margin-right: 10px;
    
}
.grid-container{
    margin-left: auto;
    margin-right: 100px;
    margin-top: 0px;
   
    width: 100%;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: auto auto auto;

   
}

.card{
    flex: 1;
   color: rgb(255, 255, 255);
   background-image: linear-gradient(to right, #0f0c29, #302b63, #24243e);
   width: 200px;
    height: 110px;
    text-align: center;
    border-bottom-left-radius: 10px;
    border-top-right-radius: 10px;

   

}

.card_name
{ 
    text-shadow: 5px 5px 5px black;
}
.card_name span{ font-size: 10px;}
.card_total_card_small{
    font-size: 25px;
    margin-top: 10px;
   
    color: rgb(243, 229, 38);
    text-shadow: 5px 5px 5px black;
}
.datetime{
    color: rgb(243, 229, 38);
    font-size: 15px;
}
